.PresaleMonitor {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;
  margin-left: 0%;
  /* border: 2px solid orange; */
}

.PresaleBox {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 450px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  background-color: var(--color3);
}

.PresaleTitle {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
  /* border: 2px solid yellow; */
}

.PresaleTitle h3 {
  font-weight: 700px;
  font-size: 20px;
  color: white;
  margin: 0;
}

.PresaleTitle p {
  font-weight: 600px;
  font-size: 12px;
  color: white;
  margin: 0;
}

.PresaleTime {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}

.PresaleTime p {
  margin: 0px;
}

.PresaleCurrentStatus {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 15px;
  /* border: 2px solid red; */
}

.Current {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Status p {
  margin: 0px;
}

.Raised {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-bottom: 3px solid var(--bluetitle);
  margin-bottom: 15px;
}

.Raised p {
  margin: 0px;
}

.Raised span {
  color: var(--color1alt);
  font-size: 17px;
}

.ProgressBar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f1f1f1;
  border-radius: 5px;
  height: 15px;
  margin-top: 10px;
  overflow: hidden;
  width: 100%;
  border: solid var(--color3);
}

.Progress {
  background-color: var(--color3);
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  transition: width 0.5s;
}

.InputContribute {
  border-radius: 10px;
  border: solid 2px var(--color0);
  padding: 7px;
  width: 20%;
}

.PresaleContributionBOHM {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.Contribution,
.BOHM {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Contribution p,
.BOHM p,
.Current p {
  margin: 0px;
}

.PresaleTime h3,
.Current h3,
.Status h3,
.Raised h3,
.Contribution h3,
.BOHM h3 {
  color: var(--graytitle);
  margin: 0px;
  font-size: 20px;
  font-weight: 500px;
  margin-bottom: 2px;
}

.PresaleTime p,
.Current p,
.Status p,
.Raised p,
.Contribution p,
.BOHM p {
  color: white;
  font-size: 24px;
  font-weight: 700;
}

.ButtonDashboard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ButtonDashboard button {
  cursor: not-allowed;
  margin-left: 10px;
  margin-right: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-transform: uppercase;
  background-color: rgba(21, 0, 112, 0.322);
  border-radius: 10px;
  border: 2px solid var(--bluetitle);
  color: white;
  font-weight: 800;
  height: 40px;
  position: relative;
  overflow: hidden;
}

.ButtonDashboard2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ButtonDashboard2 button {
  cursor: not-allowed;
  margin-left: 10px;
  margin-right: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-transform: uppercase;
  background-color: var(--color1);
  border-radius: 10px;
  border: 2px solid var(--bluetitle);
  color: white;
  font-weight: 800;
  height: 40px;
  position: relative;
  overflow: hidden;

  /* border: solid 1px red; */
}

