@media screen and (max-width: 780px) {
  .PresaleMonitor {
    width: 100%;
    height: 90vh;
    margin-left: 0px;
    /* border: 2px solid orange; */
  }
}

@media screen and (max-width: 450px) {
  .PresaleMonitor {
    width: 100%;
    height: 90vh;
    margin-left: 0px;
    /* border: 2px solid orange; */
  }

  .PresaleBox {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    background-color: rgba(0, 110, 255, 0.411);
  }

  .PresaleTitle h3 {
    font-size: 15px;
  }

  .PresaleTitle p {
    font-size: 9px;
  }

  .PresaleTime h3,
  .Current h3,
  .Status h3,
  .Raised h3,
  .Contribution h3,
  .BOHM h3 {
    font-size: 14px;
  }

  .PresaleTime p,
  .Current p,
  .Status p,
  .Raised p,
  .Contribution p,
  .BOHM p {
    font-size: 16px;
  }
}
